@import "selects";
@import "jquery.formstyler";
@import "jquery.formstyler.theme";
@import "animate";

$osn-col: #161F32;

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #3f5c86;
}

::-webkit-scrollbar-thumb:window-inactive {
  background: $osn-col;
}

input:focus::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: transparent;
}
input:focus::-moz-placeholder { /* Firefox 19+ */
  color: transparent;
}
input:focus:-ms-input-placeholder { /* IE 10+ */
  color: transparent;
}
input:focus:-moz-placeholder { /* Firefox 18- */
  color: transparent;
}

a, a:focus, button, button:focus, input, input:focus, textarea, textarea:focus {
  outline: 0 !important;
}

:focus {
  outline: 0 !important;
  outline-offset: 0 !important;
}

select, select:focus {
  outline: 0 !important;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  position: relative;
}

p {
  line-height: 100%;
  white-space: pre-line;
  font-size: $a17;
  color: #161F32;
  font-family: 'GilroyMedium';
  opacity: .7;
  //line-height : $a26;
}
.slick-next, .slick-prev {
  width : auto;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  &:before {
    color: #204479;
    font-size : $a30;
    font-weight : 900;
  }
}
a:focus, a:hover {
  text-decoration: none; }

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #000;
  cursor: pointer;
  font-size: $a18;

}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active   2.1.8 */ {
  opacity: 0;
  transition-delay: .5s;
}

.wow {
  visibility: hidden;
}



.item_icon {
  margin-top : $a40;
  .icon {
    margin-bottom : $a16;
    display: inline-block;
    padding: $a12;
    background: #4FE5E2;
    border-radius: $a12;
    img {
      width : $a32;
    }
  }
  h4 {
    margin-bottom : $a8;
  }
  p {
    line-height : $a26;
  }
}



.button, .button_form {
  font-family: "GilroyBold";
  padding: $a16;
  color: #204479;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height : $a22;
  background: #4FE5E2;
  font-size: $a18;
  border: 1px solid #4FE5E2;
  white-space: nowrap;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: $a18;
  width: $a240;
  box-shadow: 0 $a20 $a30 -$a5 rgba(79, 229, 226, 0.5);
  &:before {
    content: '';
    font-size: 0;
    transition: all 0.5s;
  }
  &.reverse {
    background: none;
    box-shadow : none;
    padding : $a10 $a32;
    &:hover {
      background: #4FE5E2;
    }
  }
  &:hover {
    background-color: #fff;
    color: #000;
  }
  &.error {
    background: red !important;
    font-size: 0;
    color: #fff !important;
    &:before {
      content: '  ';
      font-size: $a24;
    }
  }
  &.ok {
    background: green !important;
    font-size: 0;
    color: #fff !important;
    &:before {
      content: '  ';
      font-size: $a24;
    }
  }
  &.small {
    padding: 0 $a10;
    height: $a36;
    font-size: $a14;
    img , svg{
      height: $a16;
      margin-right: $a6;
      transition: all 0.5s;
    }
    &:hover {
      color: #000;
      img, svg {
        filter: invert(1);
      }
    }
  }
  &.white {
    border-color: #fff;
    background: transparent;
    &:hover {
      background: #fff;
    }
  }
}

select::-ms-expand {
  display: none;
}

body {
  position: relative;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  color: #000000;
  background: #fff;
  font-size: $a18;
  font-family: 'GilroyRegular';
  .off {
    overflow: hidden;
  }
}

.covid {
  z-index : 50;
  padding: $a15 0;
  width: 100%;
  //height: $a48;
  background: #161F32;
  position: relative;
  display: flex;
  justify-content : center;
  align-items: center;
  p {
    font-family: 'GilroyMedium';

    font-size: $a16;
    line-height: $a19;
    color: #FFFFFF;
    a {
      color: inherit;
      font-size: inherit;
      line-height: inherit;
      text-decoration: underline;
    }
  }
  .close {
    position: absolute;
    top : $a16;
    right : $a16;
    background: none;
    border: none;
    img {
      width: $a16;

    }
  }
}

#header {
  padding-top : $a10;
  z-index : 30;
  top : 0;
  width : 100%;
  position: sticky;
  transition: background 0.5s;
  &.offset {
    padding-bottom : $a10;
    background : #fff;
    border-bottom : 1px solid #00000010;
    .container {
      .phone_number {
        a {
          color: #161F32;
          &:before {
            filter: invert(1);
          }
        }
      }
      .nav {
        .menu {
          li {
            a {
              color : #161f32;
            }
          }
        }
      }
      .btn_popup {
        color : #4FE5E2;
        &:hover {
          color : #fff;
        }
      }
    }
  }
  .container {
    display: flex;
    align-items: center;
    justify-content : space-between;
    .logo {
      a {
        img {
          width : $a179;
        }
      }
    }
    .phone_number {
      a {
        position   : relative;
        font-family: 'GilroyRegular';
        font-size: $a16;
        line-height: $a22;
        color: #FFFFFF;
        transition: all 0.5s;
        &:hover {
          color : #4fe5e2;
        }
        &:before {
          position : absolute;
          content: '';
          background : url("../img/icons/header/phone.svg") no-repeat 0 0 / contain;
          width: $a16;
          height : $a18;
          left : -$a25;
          top : 50%;
          transform: translateY(-50%);
        }
      }
    }
    .nav {
      .menu {
        display: flex;
        li {
          margin: 0 $a20;
          a {
            font-family: 'GilroyRegular';
            font-style: normal;
            font-weight: bold;
            font-size: $a18;
            position: relative;
            line-height: $a22;
            color: #FFFFFF;
            opacity: 0.5;
            transition: all 0.5s;
            &:before {
              position: absolute;
              content: "";
              bottom : -$a8;
              left : -25%;
              width : 0;
              transition: all 0.5s;
              height : $a2;
              min-height: 1px;
              background: #4FE5E2;
              border-radius: 2px;
            }
            &:hover {
              opacity: 1;
              &:before {
                width : 70%;
              }
            }
          }
        }
      }
    }
    .btn_popup {
      background: none;
      padding: $a12 $a32;
      border: 2px solid #4FE5E2;
      border-radius: $a16;
      font-family: "GilroyRegular";
      font-style: normal;
      font-weight: bold;
      font-size: $a16;
      line-height: $a19;
      color: #FFFFFF;
      transition: all 0.5s;
      &:hover {
        background : #4FE5E2;
      }
    }
    .burger {
      display: none;
    }
  }
}

#footer {
  .footer_top {
    padding-top : $a48;
    padding-bottom : $a24;
    .container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      .item {
        &:nth-of-type(1) {
          width : 22%;
        }
        img {
          margin-bottom : $a16;
          width : $a128;
        }
        p {
          font-family: 'GilroyMedium';
          font-size: $a14;
          line-height: $a20;
          color: #161F32;
          white-space: normal;
        }
        h5 {
          margin-bottom : $a16;
        }
        .footer_menu {
          li {
            margin-bottom : $a12;
            display: flex;
            justify-content : flex-start;
            align-items : flex-start;
            a {
              font-family: 'GilroyMedium';
              font-size: $a16;
              line-height: $a22;
              color: #161F32;
              transition: all 0.5s;
              &:hover {
                color : #4fe5e2;
              }
            }
          }
        }
        .phone_footer, .mail_footer {
          display: flex;
          align-items: center;
          position: relative;
          font-family: 'GilroyMedium';
          font-size: $a16;
          line-height: $a22;
          color: #161F32;
          padding-left : $a25;
        }
        .phone_footer {
          transition: all 0.5s;
          margin-bottom : $a5;
          &:hover {
            color : #4fe5e2;
          }
          &:before {
            position: absolute;
            content: '';
            background : url("../img/icons/footer/phone_footer.svg") no-repeat 0 0 / contain;
            width: $a16;
            height : $a16;
            left : 0;
          }
        }
        .mail_footer {
          transition: all 0.5s;
          &:hover {
            color : #4fe5e2;
          }
          &:before {
            position: absolute;
            content: '';
            background : url("../img/icons/footer/email.svg") no-repeat 0 0 / contain;
            width: $a16;
            height : $a16;
            left : 0;
          }
        }
        .social {
          border: 2px solid #F4F4F4;
          border-radius: $a12;
          justify-content : center;
          align-items: center;
          padding: $a11 $a15;
          display: inline-flex;
          transition: all 0.5s;
          &:hover {
            background : #F4F4F4;
          }
          img {
            width : $a18;
            margin-bottom : 0;
          }
        }
        .fb {
          margin-right : $a8;
          img {
            width : $a10;
          }
        }
        .inst {
          margin-right : $a8;
        }
        .inst, .pint {
          padding: $a11 $a12;
        }
      }
    }
  }
  .footer_bottom {
    border-top: 1px solid #f9f9f9;
    .container {
      padding: $a15 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .wrap {
        display: flex;
        align-items: center;
        img {
          margin-left : $a10;
          width : $a82;
        }
      }
      p, a {
        font-family: 'GilroyMedium';
        font-size: $a14;
        line-height: $a22;
        color: #161F32;
        //opacity: 0.7;
      }
    }
  }
}

.container {
  max-width: $a1504;
  width  : 100%;
  margin : 0 auto;
}


button {
  cursor: pointer;
  &:active {
    box-shadow: inset 0 5px 10px 0 rgba(0,0,0,0.5) !important;
  }
}

h1 {
  color:#161F32;
  font-family: 'GilroyExtraBold';
  font-size: $a55;
  line-height: $a98;
  text-align: center;
}

h2 {
  font-family: 'GilroyExtraBold';
  font-size: $a40;
  line-height: $a49;
  color: #FFFFFF;
  text-transform : uppercase;
}

h3 {
  font-family: 'GilroyExtraBold';
  font-size: $a34;
  line-height: $a39;
  color: #161F32;
  text-transform : uppercase;
}

h4 {
  font-family: 'GilroyBold';
  font-size: $a20;
  line-height: $a22;
  color: #204479;
  text-transform : uppercase;
}

h5 {
  font-family: 'GilroyBold';
  font-size: $a18;
  line-height: $a19;
  color: #161F32;
  text-transform : uppercase;
}

.mob_title {
  display: none;
}

.mobile-menu {
  display: none;
}

.popup {
  position: fixed;
  width : 100%;
  height : 100%;
  background : rgba(22, 31, 50, 0.7);
  backdrop-filter: blur(10px);
  z-index : 100;
  top : 0;
  left : 0;
  transition: all 0.5s;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  display: none;
  &.select {
    form {
      &>input {
        &.m2 {
          display: block;
        }
      }
    }
  }
  form {
    width : $a500;
    background : #fff;
    border-radius: $a24;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding : $a40 $a30;
    position : relative;
    .close {
      content : '';
      position: absolute;
      top : $a15;
      right : $a15;
      width : $a20;
      cursor: pointer;
      filter: invert(1);
    }
    .title {
      font-family : 'GilroySemiBold';
      color : #161F32;
      font-size : $a40;
      opacity : 1;
      margin-bottom : $a40;
    }
    &>input {
      background: rgba(79, 229, 226, 0.2);
      border: 1px solid #4FE5E2;
      box-sizing: border-box;
      box-shadow: inset 0px 2px 2px rgba(0, 0, 0, 0.1);
      border-radius: $a16;
      width : 100%;
      height : $a60;
      padding-left : $a20;
      margin-bottom : $a15;
      font-size : $a16;
      appearance: none;
      &::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
        appearance: none;
      }
      &.m2 {
        display: none;
      }
    }
    button {
      width : 100%;
      height : $a60;
      background: #4FE5E2;
      box-shadow: 0px 20px 30px -5px rgba(79, 229, 226, 0.5);
      border-radius: $a16;
      font-family : 'GilroySemiBold';
      font-size : $a24;
      border : none;
      color : #204479;
      margin-bottom : $a30;
      img {
        height : 100%;
      }
    }
    label {
      display: flex;
      align-items: center;
      cursor: pointer;
      a {
        border-bottom : 1px solid;
      }
    }
    .jq-checkbox {
      background: rgba(79, 229, 226, 0.2);
      border: 1px solid #4FE5E2;
      box-sizing: border-box;
      box-shadow: inset 0px 2px 2px rgba(0, 0, 0, 0.1);
      border-radius: 4px;
      width : $a20;
      height : $a20;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right : $a10;
      .jq-checkbox__div {
        margin: 0;
        width : $a10;
        height : $a5;
        margin-bottom: $a2;
      }
    }
  }
}

html {
  &.openMenu {
    height : 100% !important;
    overflow-y : scroll !important;
    body {
      overflow : hidden !important;
      height : 100% !important;
    }
    .mobile-menu {
      z-index : 100;
      opacity : 1;
      .holder {
        transform: none;
      }
    }
  }
  &.openPopup {
    height : 100% !important;
    overflow-y : scroll !important;
    body {
      overflow : hidden !important;
      height : 100% !important;
    }
    .popup {
      display: flex;
      visibility : visible;
    }
  }
}

