  #footer {
    .footer_top {
      padding-bottom : $m16;
      .container {
        padding-top : $m10;
        flex-flow: row wrap;
        .item {
          width : auto !important;
          h5 {
            margin-bottom : $m12;
          }
          img {
            width : $m110;
          }
          p {
            margin: $m8 0 $m16 !important;
            font-size: $m10 !important;
            line-height: $m14 !important;
            color: #161F32 !important;
            opacity: 0.7 !important;
          }
          .social {
            border: 2px solid #f4f4f4;
            border-radius: $m12;
          }
          .fb {
            margin-right : $m5;
            padding: $m11 $m15;
            img {
              width : $m10;
            }
          }
          .inst {
            margin-right : $m5;
            padding: $m11 $m11;
            img {
              width : $m18;
            }
          }
          .pint {
            padding: $m11 $m11;
            img {
              width : $m18;
            }
          }
          .phone_footer, .mail_footer {
            font-family: 'GilroyMedium';
            font-size: $m12;
            line-height: $m22;
            color: #161F32;
            opacity: 0.7;
            padding-left : $m20;
            &:before {
              width : $m12;
              height : $m12;
            }
          }
        }
        .mob_n {
          display: none;
        }
        .w_full {
          width : 50%;
        }
      }
    }
    .footer_bottom {
      .container {
        flex-flow: row wrap;
        padding-top : $m8;
        padding-bottom : $m8;
        align-items: start;
        p {
          font-size: $m10 !important;
          line-height: inherit !important;
          color: #161F32 !important;
          opacity: 0.7 !important;
        }
        .year, .wrap, .mob_title {
          width : 50%;
        }
        .links {
          order: 3;
          margin-top : -$m13;
          a {
            font-size : $m9;
            transform: none;
          }
        }
        .wrap {
          p {
            line-height : $m16 !important;
          }
          img {
            width : $m66;
          }
        }
      }
    }
  }
}


