@import "_pxTOvw.scss";
@import "_datepicker.min.scss";
.datepicker-dropdown {
  box-shadow: 0 $a3 $a24 0 rgba(0, 0, 0, 0.09);
  background: #FFFFFF;
  border: 1px solid #4FE5E2;
  box-sizing: border-box;
  border-radius: $a16;
  overflow : hidden;
  //width : $a300;
  /*.datepicker-panel>ul[data-view="days"] {
    display: grid !important;
    grid-template-columns: repeat(7, 1fr);
    grid-column-gap: $a13;
    grid-row-gap: $a10;
    & > li[data-view="day"] {
      width         : $a25;
      height        : $a25;
      border-radius : $a5;
      font-size     : $a12;
      border        : 1px solid #4FE5E2;
    }
  }*/
}
.datepicker-panel>ul>li {
  &:hover {
    background-color : transparentize(#dcfaf9, .5);
  }
  &.highlighted {
    background-color : #dcfaf9;
  }
}
.datepicker-panel>ul>li.picked, .datepicker-panel>ul>li.picked:hover {
  color: #4fe5e2;
}
.selects_wrapper {
  padding: $a50 $a56 $a150 $a56;
  width: $a1272;
  background: #FFFFFF;
  border-radius: $a18;
  display: flex;
  position: relative;
  align-items: stretch;
  justify-content: space-between;
  &.select {
    input {
      display : none;
    }
    .jq-selectbox {
      &:nth-of-type(2) {
        display : block;
      }
    }
  }
  input {
    font-family: "GilroyMedium";
    font-size: $a16;
    line-height: $a19;
    color: #161F32;
    //opacity: 0.7;
    width: $a215;
    padding: $a16;
    cursor: pointer;
    background: rgba(79, 229, 226, 0.2);
    border: 1px solid #4FE5E2;
    box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.1);
    border-radius: $a16;
    cursor: auto;;
  }
  .selects-holder {
    display: flex;
    width: $a215;
    .v-select {
      width : 50%;
      &.datepicker {
        font-family: "GilroySemiBold";
        font-size: $a16;
        color: #161F32;
        padding : 0 $a16 0 0;
        p {
          white-space: nowrap;
          overflow : hidden;
          text-overflow: ellipsis;
          padding : $a16 0 $a16 $a16;
        }
        img {
          width : $a10;
        }
      }
      &:first-of-type {
        border-radius: $a16 0 0 $a16;
        .v-list-dropdown {
          left : -1px;
        }
        &.open {
          border-radius: $a16 0 0 0;
        }
      }
      &:last-of-type {
        border-radius: 0 $a16 $a16 0;
        .v-list-dropdown {
          right : -1px;
        }
        &.open {
          border-radius: 0 $a16 0 0;
        }
      }
    }
  }
  .v-select {
    font-family: "GilroySemiBold";
    font-size: $a16;
    line-height: $a19;
    //opacity: 0.7;
    width: $a215;
    height : $a55;
    padding: $a16;
    cursor: pointer;
    background: rgba(79, 229, 226, 0.2);
    box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.1);
    border: 1px solid #4FE5E2;
    position : relative;
    display: flex;
    align-items: center;
    .text {
      display: flex;
      width : 100%;
      .title {
        width : 100%;
        p {
          font-family: "GilroySemiBold";
          font-size: $a16;
          color: #161F32;
          opacity : .7;
          white-space: nowrap;
          overflow : hidden;
          text-overflow: ellipsis;
          max-width : 90%;
        }
      }
      img {
        width : $a10;
        margin-left: auto;
      }
    }
    .v-list-dropdown {
      background: #fff;
      box-shadow: 0 $a3 $a24 0 rgba(0, 0, 0, 0.09);
      border: 1px solid #204479;
      border-top: 1px solid #4FE5E255;
      color: #000;
      border-radius: 0 0 $a16 $a16;
      transform: translateY(-$a1);
      box-sizing: border-box;
      z-index: 1;
      margin-top : 0;
      overflow : hidden;
      position: absolute;
      top : 100%;
      display: none;
      ul {
        max-height : $a300;
        overflow : auto;
        &::-webkit-scrollbar {
          width: 2px;
          border-radius : 2px;
        }

        &::-webkit-scrollbar-track {
          background: #fff;
        }

        &::-webkit-scrollbar-thumb {
          background: #204479;
          border-radius : 20px;
          width: 2px;
        }

        &::-webkit-scrollbar-thumb:window-inactive {
          background: #204479;
          border-radius : 20px;
          width: 2px;
        }
        li {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          padding: $a10 $a16;
          transition: all 0.5s;
          cursor: pointer;
          opacity : .7;
          font-weight : 500;
          font-family : 'GilroyRegular';
          white-space: nowrap;
          span {
            margin-left: auto;
            padding-left : $a10;
          }
          &.active {
            color: #000;
            background : #4FE5E250;
            font-weight : bold;
          }
          &:hover:not(.disabled) {
            background : rgba(0,0,0,.1);
            color : #000;
          }
        }
      }
    }
    &.open {
      border-radius: $a16 $a16 0 0;
      background : #fff;
      border-color : #204479;
      border-bottom : 1px solid #4FE5E255;
      .text {
        .title {
          p {
            opacity : 1;
          }
        }
      }
      .v-list-dropdown {
        display: block;
      }
    }
  }
  .jq-selectbox {
    font-family: "GilroySemiBold";
    font-size: $a16;
    line-height: $a19;
    //opacity: 0.7;
    width: $a215;
    cursor: pointer;
    background: rgba(79, 229, 226, 0.2);
    box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.1);
    border-radius: $a16;
    .jq-selectbox__select-text {
      font-family: "GilroySemiBold";
      font-size: $a16;
      color: #161F32;
      opacity : .7;
    }
    /*&:last-of-type {
      .jq-selectbox__dropdown {
        border-top : 0;
        border-radius: 0 0 $a16 $a16;
        width : 100% !important;
        &:before {
          content    : '';
          position   : absolute;
          width      : 100%;
          top        : 0;
          right      : 0;
          height     : 1px;
          background : linear-gradient(270deg, #204479 50.2%, #4FE5E2 50%);
        }
        ul {
          li {
            width : 100%;
            white-space: normal;
          }
        }
      }
    }*/
    &.opened {
      border-radius: $a16 $a16 0 0;
      background : #fff;
      &.dropup {
        border-radius: 0 0 $a16 $a16;
        .jq-selectbox__select {
          border : 1px solid #204479;
          border-top : 1px solid #4FE5E255;
          border-radius: 0 0 $a16 $a16;
        }
        .jq-selectbox__dropdown {
          border-radius : $a16 $a16 0 0;
          border: 1px solid #204479;
          border-bottom: 1px solid #4FE5E255;
        }
      }
      .jq-selectbox__select-text {
        opacity : 1;
      }
      .jq-selectbox__select {
        border-color : #204479;
        border-bottom : 1px solid #4FE5E255;
        border-radius: $a16 $a16 0 0;
        .jq-selectbox__trigger {
          padding-bottom: 0;
          padding-top: $a5;
          .jq-selectbox__trigger-arrow {
            transform: rotate(45deg);
          }
        }
      }
    }
    select {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      opacity: 0;
    }
    .jq-selectbox__select {
      border: 1px solid #4FE5E2;
      border-radius: $a16;
      background : none;
      padding: $a16;
      width : auto;
      height : $a24;
      box-sizing: content-box;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow : none;
      .jq-selectbox__trigger {
        position : static;
        width : auto;
        height: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;
        transition: all 0.5s;
        border : none;
        padding-bottom : $a5;
        .jq-selectbox__trigger-arrow {
          transform: rotate(225deg);
          width: $a7;
          height: $a7;
          border : none;
          position: static;
          border-top: $a1 solid #000;
          border-left: $a1 solid #000;
          transition: all 0.5s;
        }
      }
    }
    .jq-selectbox__dropdown {
      background: #fff;
      box-shadow: 0 $a3 $a24 0 rgba(0, 0, 0, 0.09);
      border: 1px solid #204479;
      border-top: 1px solid #4FE5E255;
      width : 100% !important;
      color: #000;
      border-radius: 0 0 $a16 $a16;
      transform: translateY(-$a1);
      box-sizing: border-box;
      z-index: 1;
      margin-top : 0;
      overflow : hidden;
      ul {
        &::-webkit-scrollbar {
          width: 2px;
          border-radius : 2px;
        }

        &::-webkit-scrollbar-track {
          background: #fff;
        }

        &::-webkit-scrollbar-thumb {
          background: #204479;
          border-radius : 20px;
          width: 2px;
        }

        &::-webkit-scrollbar-thumb:window-inactive {
          background: #204479;
          border-radius : 20px;
          width: 2px;
        }
        li {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          padding: $a10 $a16;
          transition: all 0.5s;
          cursor: pointer;
          &.sel {
            color: #000;
            background : #4FE5E250;
          }
          &:hover:not(.disabled) {
            background : rgba(0,0,0,.1);
            color : #000;
          }
        }
      }
    }
  }
  select {
    font-family: "GilroyMedium";
    font-size: $a16;
    line-height: $a19;
    color: #161F32;
    //opacity: 0.7;
    width: $a215;
    padding: $a16;
    cursor: pointer;
    background: rgba(79, 229, 226, 0.2);
    border: 1px solid #4FE5E2;
    box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.1);
    border-radius: $a16;
  }
}

@media screen and (max-width: 1023px) {
  .selects_wrapper {
    padding        : 0 $m24;
    border-radius  : $m24;
    width          : 100%;
    flex-direction : column;
    input {
      margin-bottom : $m8;
      border-radius : $m16;
      width         : 100%;
      padding       : 0 $m20;
      height        : $m45;
      font-size     : $m14;
      line-height   : inherit;
    }
    .selects-holder {
      width : 100%;
      height        : $m45;
      margin-bottom : $m8;
      position : relative;
      .v-select {
        height : 100%;
        padding       : 0 $m20;
        &.datepicker {
          p {
            font-size : $m14;
            padding-left : $m20;
          }
          &~.v-select {
            position: static;
            .v-list-dropdown {
              left : 50%;
              transform: translateX(-50%);
              margin: auto;
              width : 95vw;
              ul {
                li {
                  white-space: normal;
                  span {
                    text-align : right;
                  }
                }
              }
            }
          }
        }
        &:first-of-type {
          border-radius : $m16 0 0 $m16;
          &.open {
            border-radius : $m16 0 0 0;
          }
        }
        &:last-of-type {
          border-radius : 0 $m16 $m16 0;
          &.open {
            border-radius : 0 $m16 0 0;
          }
        }
        .text {
          .title {
            max-width : 85%;
            p {
              font-size     : $m14;
              white-space: nowrap !important;
            }
          }
          img {
            width : $m12;
          }
        }
        .v-list-dropdown {
          ul {
            max-height : $m200;
            li {
              font-size : $m12;
              padding : $m10 $m20;
              height: auto;
              line-height : initial;
            }
          }
        }
      }
    }
    .jq-selectbox {
      margin-bottom : $m8;
      border-radius : $m16;
      width         : 100%;
      .jq-selectbox__select-text {
        font-size : $m14;
      }
      &.opened {
        border-radius : $m16;
        .jq-selectbox__select {
          border-radius : $m16 $m16 0 0;
        }
        &.dropup {
          border-radius : 0 0 $m16 $m16;
          .jq-selectbox__select {
            border-radius : 0 0 $m16 $m16;
          }
          .jq-selectbox__dropdown {
            border-radius : $m16 $m16 0 0;
            top           : auto !important;
          }
        }
      }
      .jq-selectbox__select {
        padding       : 0 $m20;
        height        : $m45;
        border-radius : $m16;
        color         : #161F32 !important;
        opacity       : 0.7 !important;
        .jq-selectbox__trigger {
          .jq-selectbox__trigger-arrow {
            width        : $m8;
            height       : $m8;
            border-width : $m2;
          }
        }
      }
      .jq-selectbox__dropdown {
        border-radius : 0 0 $m16 $m16;
        top           : 100% !important;
        ul {
          &::-webkit-scrollbar {
            width         : $m2;
            border-radius : 2px;
          }

          &::-webkit-scrollbar-thumb {
            width : $m2;
          }

          &::-webkit-scrollbar-thumb:window-inactive {
            width : $m2;
          }
          li {
            padding : $m10 $m20;
          }
        }
      }
    }
  }
}
